.animation-container {
  display: none;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  
}

.animation-container img {
  animation: pokeballAnimation 1.2s infinite;
}

@media screen and (max-width: 728px) {
.animation-container img {
  width: 280px;
  }
}